.userList{
    flex: 6.5;
    display: flex;
    flex-direction: column;
    margin: 20px;
    padding: 20px;
}
.UserListuser{
    display: flex;
    align-items: center;
}

.UserListTable{
    padding: 20px;
    margin: 20px;
    box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75);
    -webkit-box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75);
    border-radius: 10px;
   
    
}
.UserListImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
 
}
.BtnListEdit{
    width: 100px;
    border: none;
    background-color: cadetblue;
    height: 35px;
    color: white;
    border-radius: 5px;
    font-weight: bold;
    margin-right: 20px;
}
.BtnListDelete{
width: 32px;
height: 32px;
color: rgb(139, 21, 21);
cursor: pointer;
}
.inputsearch{
    background-color: lightgray;
    padding: 5px;
    margin: 5px;
    color: black;
    border: none;
    font-size: 14px;
    font-weight: 600;
    

}