.newUser {
  flex: 6.5;
}
.newUserTitle{
  text-align: center;
  margin-top: 2px;
}
.newUserForm {
  display: flex;
  flex-direction: column;
  
  justify-content: center;
  align-items: center;
 
}

.newUserItem {
  width: 400px;
  display: flex;
  flex-direction: column;
  margin-top: 5px;
  
  padding: 10px;
  
}

.newUserItem > label {
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: 600;
  color: rgb(151, 150, 150);
}

.newUserItem > input {
  height: 20px;
  padding: 10px;
  border: 1px solid gray;
  border-radius: 5px;
}

.newUserGender > input {
  margin-top: 15px;
}

.newUserGender>label{
    margin: 10px;
    font-size: 18px;
    color: #555;
}

.newUserSelect{
    height: 40px;
    border-radius: 5px;
}

.newUserButton{
    width: 200px;
    border: none;
    background-color: rgba(48, 48, 187, 0.466);
    color: white;
    padding: 7px 10px;
    font-weight: 600;
    border-radius: 10px;
    margin-top: 30px;
    cursor: pointer;
    margin: 5px;
}